<template>
	<surveyQuestionWrapper :question="question">
		<input class="text" @blur="blur($event, question)" :value="value" :placeholder="question.placeHolder||'请输入'" 
			:type="question.inputType === 'number' ? 'digit' : question.inputType" />
	</surveyQuestionWrapper>
</template>

<script>
	import surveyQuestionWrapper from './wrapper';
	
	export default {
		data() {
			return { };
		},
		props: {
			question: { type: Object, default: () =>  { } },
			result: { type: String, default: "" }
		},
		computed: {
			value() {
				return this.result;
			}
		},
		methods: {
			blur(event) {
				const { value } = event.target;
				this.$emit('change', { value });
			}
		},
	}
</script>

<style>
.text {
	font-size: 18px;
	padding-bottom: 20px;
	color: #171717;
	line-height: 52px;
}
</style>
